import React, {FC} from "react";
import Modal from "react-modal";

const StartForm: FC = () => {
  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
      <div>
          <main className="main">
            <h1>
              <div className="main__title">
                 <strong className="colorize"> </strong>   
              </div>
            </h1>

            <div className="main__cards">
              <button className="main__button-new" onClick={openModal}> </button>
              <button className="main__button-cont"> </button>
            </div>
          </main>

        <Modal
            className="popup-fade"
            isOpen={modalIsOpen}
            ariaHideApp={false}
            onRequestClose={closeModal}
        >
            <div className="popup-fade__popup">
                <button className="popup-fade__popup-close" onClick={closeModal}>
                    <svg width="3vw" height="3vw" viewBox="0 0 23 26" fill="none"
                         xmlns="http://www.w3.org/2000/svg">
                        <path d="M1.89014 23.991L21.8901 1.991M21.8901 24.009L1.89014 2.00902" stroke="black"
                              stroke-width="3"/>
                    </svg>
                </button>
                <div className="popup-fade__inputs">
                    <label htmlFor="name" className="popup-fade__input">  *</label>
                    <input type="text" id="name" className="modal-input"
                           placeholder="   ( 100 )" maxLength={100} />
                    <label htmlFor="url" className="popup-fade__input">URL </label>
                    <input type="url" id="url" className="modal-input" placeholder="http://" />
                    <label htmlFor="plt" className="popup-fade__input"></label>
                    <input type="text" id="plt" className="modal-input"
                           placeholder=" ( 50 )" maxLength={50} />
                    <label htmlFor="auth" className="popup-fade__input"></label>
                    <input type="text" id="auth" className="modal-input" placeholder=" .." />
                    <label htmlFor="char" className="popup-fade__input">  </label>
                    <textarea id="char" className="modal-input"
                              placeholder=" .   ( 200 )"
                              maxLength={200} />
                </div>
                <button className="popup-fade__start"></button>
                <p className="popup-fade__ps">* -   </p>
            </div>
        </Modal>
      </div>
  )
};

export default StartForm;
